import axois from 'axios';
import db from '../../db/gets.json'
const baseurl = 'http://sandani.local';



export const trafik = () => {
    return (dispatch) => {
        axois.get(`${baseurl}${db.getTrafik}`)
            .then((res) => dispatch({ type: 'TRAFIK', payload: res }))
    }
}
